import jsCookie from 'js-cookie'
import queryString from 'query-string'

import {
  getUserInfo,
  getOrderList,
  getOrderDetail,
} from '@/api'

function getCookie () {
  const cookie = jsCookie.get()
  const { userId = '', userToken = '' } = cookie

  return {
    userId,
    userToken,
  }
}

function getUrlParam () {
  const url = window.location.search
  const query = queryString.parse(url)
  const { uid = '' } = query

  return {
    uid,
  }
}

export default {
  async getUserInfo ({ commit }) {
    try {
      const json = await getUserInfo({
        ...getCookie(),
        ...getUrlParam(),
      })

      const { data } = json

      commit('setUserInfo', data)

      return json
    } catch (error) {
      throw error
    }
  },
  async getOrderList ({ commit }) {
    try {
      const json = await getOrderList({
        ...getCookie(),
        ...getUrlParam(),
      })

      const { data } = json

      commit('setOrderList', data)

      return json
    } catch (error) {
      throw error
    }
  },
  async getOrderDetail ({ commit }, { orderId }) {
    try {
      const json = await getOrderDetail({
        orderId,
        ...getCookie(),
        ...getUrlParam(),
      })

      const { data } = json

      commit('setOrderDetail', data)

      return json
    } catch (error) {
      throw error
    }
  },
}
