import fetch from 'isomorphic-fetch'
import queryString from 'query-string'
import { Loading, Message } from 'element-ui'
import conf from './config'

function fetchTimeout (fetchPromise, timeout) {
  const timeoutPromise = new Promise(((resolve, reject) => {
    setTimeout(() => {
      const response = {
        status: '408',
        statusText: 'fetch timeout',
      }

      const error = new Error(response.statusText)
      error.res = response

      reject(error)
    }, timeout)
  }))

  return Promise.race([fetchPromise, timeoutPromise])
}

function checkStatus (response) {
  if (response.status >= 200 && response.status < 300) {
    return response
  }

  const error = new Error(response.statusText)
  error.res = response

  throw error
}

/**
 * [fetchFunc description]
 * @param  url     [请求地址]
 * @param  data    [param数据]
 * @param  options [配置参数]
 * @return         [Promise<any>]
 */

export default async function fetchFunc (url, data, options) {
  try {
    const reqUrl = (data && Object.keys(data).length > 0) ? `${url}?${queryString.stringify(data)}` : url

    const reqOptions = {
      returnJSON: true,
      showLoading: true,
      errorAlert: true,
      isSuccess: code => code === 0,
      needLogin: code => code === 401,
      timeout: 1000,
      ...conf,
      ...options,
    }

    let loadingInstance
    if (reqOptions.showLoading) {
      loadingInstance = Loading.service({
        fullscreen: true,
      })
    }

    let response
    try {
      response = await fetchTimeout(fetch(reqUrl, reqOptions), reqOptions.timeout)
    } catch (error) {
      const { res } = error

      if (reqOptions.needLogin(res.status)) {
        window.location.replace(`/login?referer=${encodeURIComponent(window.location.href)}`)
      } else if (reqOptions.errorAlert) {
        Message({
          message: error.message || 'Network anomaly',
          type: 'error',
        })
      }

      throw error
    }

    if (loadingInstance) {
      loadingInstance.close()
    }

    const res = checkStatus(response)

    if (reqOptions.returnJSON) {
      const json = await res.json()

      if (reqOptions.needLogin(json.code)) {
        window.location.replace(`/login?referer=${encodeURIComponent(window.location.href)}`)
      } else if (!reqOptions.isSuccess(json.code) && reqOptions.errorAlert) {
        const { message = 'System anomaly' } = json

        Message({
          message,
          type: 'error',
        })
      }

      return json
    }

    return res
  } catch (error) {
    throw error
  }
}
