// [fetch](https://developer.mozilla.org/en/docs/Web/API/WindowOrWorkerGlobalScope/fetch)

/**
* method 请求使用的方法，如 GET、POST 等。默认 GET
* headers 请求的头信息，形式为 Headers 的对象或包含 ByteString 值的对象字面量
* body  请求的 body 信息：可能是一个 Blob、BufferSource、FormData、URLSearchParams 或者 USVString 对象。注意 GET 或 HEAD 方法的请求不能包含 body 信息
* mode 请求的模式，如 cors、 no-cors 或者 same-origin
* credentials 请求的 credentials，如 omit、same-origin 或者 include。默认 omit
* etc.
*/

export default {
  method: 'GET',
  credentials: 'include', // 设置 omit 不带 cookie, 设置 include 带 cookie
}
