import Vue from 'vue'
import Router from 'vue-router'
import NProgress from 'nprogress'
import 'nprogress/nprogress.css'
import jsCookie from 'js-cookie'

import routes from './routes'

NProgress.configure({
  easing: 'ease', // 动画方式
  minimum: 0.3, // 初始化时的最小百分比
  speed: 500, // 递增进度条的速度
  showSpinner: false, // 是否显示加载icon
  trickleSpeed: 200, // 自动递增间隔
})

Vue.use(Router)

const router = new Router({
  mode: 'hash', // 'history',
  base: process.env.BASE_URL,
  routes,
})

router.beforeEach(async (to, from, next) => {
  NProgress.start()

  const cookie = jsCookie.get()
  const { userId = '', userToken = '' } = cookie

  console.log(to)

  if (userId && userToken) {
    if (to.path === '/login') {
      next('/orderList')
    } else {
      next()
    }
  } else if (to.path === '/login') {
    next()
  } else {
    next(`/login?referer=${encodeURIComponent(window.location.href)}`)
  }
})

router.afterEach(() => {
  NProgress.done()
})

export default router
