// import queryString from 'query-string'
import { fetch } from '@/plugins/fetch'

const isProd = process.env.NODE_ENV === 'production'
const isMock = true

const baseUrl = isProd ? '' : '/admin'

function getUserInfo (data) {
  const url = isMock ? `/mock/user/${data.userId}.json` : `${baseUrl}/api/userInfo`

  return fetch(url, data)
}

function getOrderList (data) {
  const url = isMock ? '/mock/order/list.json' : `${baseUrl}/api/orderList`

  return fetch(url, data)
}

function getOrderDetail (data) {
  const url = isMock ? `/mock/order/${data.orderId}.json` : `${baseUrl}/api/orderDetail`

  return fetch(url, data)
}

export {
  getUserInfo,
  getOrderList,
  getOrderDetail,
}

export default {
  getUserInfo,
  getOrderList,
  getOrderDetail,
}
